.body{
    background-color: antiquewhite;
}
#home{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.header{
    width: 1000px;
    margin: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.logo{
    width: 160px;
    height: 80px;
    overflow: hidden;
}
.login{
    cursor: pointer;
    margin-right: 20px;
}
.searchbar > input{
    width: 500px;
}
.img{
    width: 329px;
    height: 248px;
    overflow: hidden;
    border-radius: 10px;
    margin-bottom: 2px;
}

.img > img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.card{
    height: 330px;
    width: 330px;
    padding: 10px;
}
.card:hover{
    border: lightgrey solid 2px;   
    border-radius: 10px;
    cursor: pointer;
}
.res{
    display: flex;
    flex-direction: column;
    gap: 2px;
}
.resinfo{
    display: flex;
    justify-content: space-between;
}
.bodydiv{
    width: 1080px;
    display: flex;
    flex-direction: column;
    gap: 5px;
    
}
.cardholder{
    display: flex;
    flex-wrap: wrap;
    width: 1080px;
}
.filter{
    width: 1080px;
    padding-left: 20px;
}